.hero {
  margin: 0 auto;
  background-color: #f5faff;
  @media screen and (min-width: 480px) {
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 100%;
    position: relative;
    width: 768px;
  }

  @media screen and (min-width: 1200px) {
    padding-bottom: 135px;
    min-width: 1920px;
  }
}
.hero_advertisement--list {
  margin-bottom: 50px;
  @media screen and (min-width: 480px) {
    margin-bottom: 50px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 0;
  }
}
.hero_advertisement--item {
  z-index: 2;
  display: flex;
  flex-direction: column-reverse;
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    margin-left: 129px;
    flex-direction: row;
  }
  @media screen and (min-width: 1200px) {
    margin-left: 367px;
    margin-right: 90px;
  }
}
.hero__sliderBox {
  margin-left: 50px;
  margin-right: 90px;
  margin-top: 30px;
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    margin-left: 0px;
    margin-right: 92px;
    margin-top: 51px;
  }
  @media screen and (min-width: 1200px) {
    margin-right: 295px;
    margin-top: 184px;
  }
}
.hero__sliderBox--title {
  line-height: 1.17em;
  font-size: 16px;
  font-weight: 700;
  &-color {
    color: $accent-color;
  }
  @media screen and (min-width: 390px) {
    font-size: 24px;
  }
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    font-size: 18px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 40;
  }
}
.hero__sliderBox--descr {
  line-height: 1.34em;
  margin-bottom: 10px;
  font-size: 8px;
  margin-top: 5px;
  &-color {
    color: $accent-color;
  }
  @media screen and (min-width: 390px) {
    font-size: 10px;
  }
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    font-size: 12px;
    margin-top: 10px;
    margin-bottom: 25px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 15px;
    margin-bottom: 40px;

    font-size: 20px;
  }
}

.hero__sliderBox--order {
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1200px) {
    display: flex;
  }
}
.hero__sliderBox--btn {
  background-color: $accent-color;
  border-color: transparent;
  border-radius: 40px;
  box-shadow: 0px 0px 40px 0px #5656564d;

  line-height: 1.34em;
  font-weight: 700;
  font-size: 14px;
  padding: 7px 29px;
  color: $light-font-color;
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    font-size: 12px;
    padding: 7px 31px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 26px;
    padding: 14px 68px;
  }
}

.hero__sliderBox--price {
  line-height: 1.34em;
  font-size: 10px;
  margin-top: 8px;
  &-color {
    line-height: 1em;
    font-weight: 700;
    font-size: 16px;
    color: $accent-color;
  }
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    margin-top: 10px;
    font-size: 12px;
    &-color {
      font-size: 18px;
    }
  }
  @media screen and (min-width: 1200px) {
    margin-top: 0;
    margin-left: 30px;
    font-size: 20px;
    &-color {
      margin-top: 0;
      font-size: 35px;
    }
  }
}

.hero__slider--img {
  // position: relative;
  // z-index: 2;
  // border: 7px solid #f5faff;
  // top: -10px;
  display: block;
  width: 37%;
  // min-width: 121px;
  border-radius: 50%;
  // margin-right: 10px;
  // margin: 0 auto;
  // margin-left: auto;
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    width: 293px;
    margin-right: 30px;
  }
  @media screen and (min-width: 1200px) {
    margin: 0;
    width: 668px;
  }
}
.hero__sliderbox--photoBox {
  background-color: $accent-color;
  margin-left: 100px;
  min-height: 130px;
  border-bottom-left-radius: 200px;
  padding-top: 10px;
  padding-bottom: 10px;
  // width: 100%;
  // height: 100%;
}
// foodConsturctor CSS table/desctop// ----------------------------------

.hero__foodConstuctor--list {
  position: absolute;
  top: 0px;
  left: 50px;
  display: none;
  @media screen and (min-width: 480px) {
  }
  @media screen and (min-width: 768px) {
    display: block;
  }
  @media screen and (min-width: 1200px) {
  }
}
.hero__foodConstuctor--item {
  box-shadow: 0px 0px 30px 0px #56565640;
  border-radius: 50%;
  justify-content: center;
  background-color: $light-font-color;
  @media screen and (min-width: 768px) {
    width: 41px;
    height: 41px;
  }
  @media screen and (min-width: 1200px) {
    width: 70px;
    height: 70px;
  }
}
.hero__foodConstructor--descr {
  display: none;
}

.hero__foodConstuctor--item:hover,
.hero__foodConstuctor--item:focus {
  fill: $accent-color;
  width: auto;
  border-radius: 70px;
  & .hero__foodConstructor--descr {
    display: block;
    margin-left: 40px;
    margin-right: 40px;
  }
  & .hero__foodConstuctor--btn {
    display: flex;
    align-items: center;
    color: $accent-color;
  }
  & .hero__foodConstuctor--btn-text {
    display: none;
  }
}

.hero__foodConstuctor--item:not(:last-child) {
  @media screen and (min-width: 768px) {
    margin-bottom: 10px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 50px;
  }
}
.hero__foodConstuctor--btn {
  //   display: flex;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border-color: transparent;
  background-color: transparent;
  &:first-child {
    font-weight: 700;
    line-height: 1.38em;
    color: $primary-color;
    @media screen and (min-width: 768px) {
      font-size: 10px;
    }
    @media screen and (min-width: 1200px) {
      font-size: 20px;
    }
  }
}
.hero__foddConstructor--btn-box {
  align-items: center;
  text-align: center;
  justify-content: center;
}
.hero__foodConstuctor--item:last-child {
  @media screen and (min-width: 768px) {
    & svg {
      height: 11px;
    }
    & span {
      font-size: 16px;
      font-weight: 300;
      line-height: 1em;
    }
    @media screen and (min-width: 1200px) {
      & svg {
        height: 20px;
      }
      & span {
        font-size: 26px;
        font-weight: 300;
        line-height: 1em;
      }
    }
  }
}

.hero__foodConstuctor--svg {
  @media screen and (min-width: 768px) {
    width: 25px;
    height: 25px;
  }
  @media screen and (min-width: 1200px) {
    width: 40px;
    height: 40px;
  }
}
// foodConsturctor CSS mobile// ----------------------------------
.hero-foodConstuctor-positionBox {
  position: relative;
  background-color: #dee6ee;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  margin-top: 57px;
  margin: 0 auto;
  height: 55px;
  @media screen and (min-width: 380px) {
    height: 70px;
  }
  @media screen and (min-width: 440px) {
    height: 70px;
  }
  @media screen and (min-width: 768px) {
    display: none;
  }
}
.hero__foodConstuctor--list-mob {
  position: absolute;
  // transform: translate(2%, -30%);
  left: 50%;
  // transfrom: translateX(-50%);
  transform: translateX(-50%);
  top: -40%;
  transform: translateY(50%);
  display: flex;
  // position: relative;
  // background-color: #dee6ee;
  // border-top-left-radius: 40px;
  // border-top-right-radius: 40px;
  margin: 0 auto;
  // margin-top: 57px;
  justify-content: center;
  @media screen and (min-width: 350px) {
    // transform: translate(8%, -40%);
  }
}
.hero__foodConstuctor--item-mob {
  justify-content: center;
  align-items: center;
  &:not(:last-child) {
    margin-right: 8.5px;
  }
  &:first-child {
    display: flex;
    flex-direction: column;
    margin-right: 4;
  }
  @media screen and (min-width: 380px) {
    &:first-child {
      margin-right: 8px;
    }
  }
  @media screen and (min-width: 440px) {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}
.hero__foodConstuctor--btn-mob {
  // border-color: black;
  border: none;
  box-shadow: 0px 0px 30px 0px #56565640;
  border-radius: 50%;
  width: 41px;
  height: 41px;
  margin: 0 auto;
  font-size: 10px;
  line-height: 1.38em;
  font-weight: 700;
  @media screen and (min-width: 380px) {
    width: 50px;
    height: 50px;
    font-size: 12px;
  }
  @media screen and (min-width: 440px) {
    width: 60px;
    height: 60px;
    font-size: 14px;
  }
  // margin-bottom: 20px;
  // &:first-child {
  //   margin-bottom: 5px;
  // }
  &:hover,
  &:focus {
    fill: $accent-color;
    color: $accent-color;
  }
}
.hero__foodConstuctor--svg-mob {
  width: 20px;
  height: 20px;
  @media screen and (min-width: 380px) {
    width: 28px;
    height: 28px;
  }
  @media screen and (min-width: 440px) {
    width: 37px;
    height: 37px;
  }
}
