//   height: 11px;
// }
.hero__foodConstructor--descr-mob {
  text-align: center;
  font-size: 7px;
  font-weight: 700;
  line-height: 1.34em;
  margin-top: 9px;
}
.marginTop {
  margin-top: 5px;
}
